.Sidebars{
    display: flex;
    flex-direction: column;
    position: relative;
    padding-top: 4rem;
    transition: all 300ms ease;
}

.logo{
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    font-size: 22px;
    align-items: center;
    justify-content: center;
    gap: 1rem;
    height: 4%;
}


.logo>img{
    width: 3rem;height: 3rem;
}

.logo>span>span{
    color: var(--pink);
}

/* menu */

.menu{
    margin-top: 4rem;
    display: flex;
    flex-direction: column;
    gap: 2rem;
}

.menuItem{
    display: flex;
    align-items: center;
    gap: 1rem;
    margin-left: 2rem;
    height: 2.5rem;
    position: relative;
    transition: all 300ms ease;
    font-size: 14px;
    border-radius: .7rem;
}

.menuItem:hover{
    cursor: pointer;
}

.active{
    background: var(--activeItem);
    margin-left: 0;
}

.active::before{
    content: '';
    width: 8px;
    height: 100%;
    background-color: var(--pink);
    margin-right: calc(1rem - 8px);
}
